import Head from "next/head";
import { useRouter } from "next/router";
import React, { useState, useEffect } from "react";
import { useSelector } from "react-redux";

const CTitleLoading = () => {
  const router = useRouter();
  const [titlePage, setTitlePage] = useState("");
  const { title } = useSelector((state) => state.title);

  useEffect(() => {
    let path = router.pathname.slice(1);
    path = path.replaceAll("/[id]", "");
    path = path.replaceAll("/", " > ");
    path = path.replaceAll("-", " ");
    let toArray = path.split(" ");
    var toCapitalize = "Dashboard";
    if (path !== "") {
      toCapitalize = toArray
        .map((word) => {
          return word[0].toUpperCase() + word.substring(1);
        })
        .join(" ");
    }
    setTitlePage(toCapitalize);
  }, []);

  return (
    <Head>
      <title>{titlePage === "" ? title : titlePage + " | " + title}</title>
      <meta
        property="og:title"
        content={titlePage === "" ? title : titlePage + " | " + title}
      />
    </Head>
  );
};
export default CTitleLoading;
